import './modules/burger';
import './modules/up';
import initSlider from './modules/slider';
import initBlogCard from './modules/blogs';
import blogsMore from './modules/blogsMore';

document.addEventListener('DOMContentLoaded', () => {
    initSlider();
    const blogCards = document.querySelectorAll('.blog-item');
    blogCards.forEach(function (blogCard) {
        initBlogCard.call(this, blogCard);
    });
    blogsMore('.blog-view', '.blog-item-hidden');
});